body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  max-width: 700px;
}
.top {
  text-align: center;
  /* border-bottom: 5px solid rgb(91, 91, 230); */
  border-width: 5px;
  position: relative;
}
.top:after {
  content: " ";
  position: absolute;
  width: 60%;
  height: 5px;
  left: 20%;
  bottom: -10px;
  background-color: rgb(97, 97, 223);
}

.about {
  margin: 20px 0px 20px 0px;
  width: 100%;
  border-radius: 5px;
  box-shadow: 0px 0px 10px black;
}
.about section {
  padding: 30px;
  display: flex;
  flex-direction: column;
}
.about section h2 {
  font-size: 100%;
}
.about section h2 span {
  float: right;
  color: blue;
  padding: 2px;
  border-radius: 5px;
  background-color: rgba(78, 85, 194, 0.3);
}
.about img {
  width: 100%;
  border-radius: 5px 5px 0px 0px;
}
.btn {
  border: 1px solid red;
  background-color: white;
  border-radius: 5px;
  font-size: 110%;
  padding: 5px;
  color: red;
  width: 50%;
  align-self: center;
}
.expand {
  border: none;
  background-color: white;
  color: rgb(91, 91, 230);
  font-size: 110%;
}
button {
  cursor: pointer;
}
